.customService-contact {
    display: flex;
    margin-top: 60px;
    -webkit-box-shadow: 0px 0px 84px -9px rgba(0, 0, 0, 0.39);
    -moz-box-shadow: 0px 0px 84px -9px rgba(0, 0, 0, 0.39);
    box-shadow: 0px 0px 84px -9px rgba(0, 0, 0, 0.39);
}

.customService-contact ul {
    list-style: none;
    display: flex;
    margin: auto;
    padding-top: 15px;
}

.customService-contact ul li {
    /* width: 300px; */
    padding: 15px;
    font-family: 'Montserrat', sans-serif;
}

.customService-deliver {
    width: 1px;
    height: auto;
    border-right: solid 1px #ccc;
}

.customService-contact-item {
    border-right: solid 1px #ccc;
    padding-right: 30px;
    /* padding-left: 30px; */
}

.customService-contact ul li:last-child .customService-contact-item {
    border: none;
}

.customService-contact-banner {
    -webkit-box-shadow: 0px 79px 21px -78px rgba(0,0,0,1);
    -moz-box-shadow: 0px 79px 21px -78px rgba(0,0,0,1);
    box-shadow: 0px 79px 21px -78px rgba(0,0,0,1);
    margin-bottom: 30px;
}

.customService-contact-banner img {
    width: 100%;
    /* margin-bottom: 15px; */
}

.ant-carousel .slick-slide {
    text-align: center;
    /* height: 160px;
    line-height: 160px; */
    /* background: #364d79; */
    overflow: hidden;
}

.ant-carousel .slick-slide h3 {
    color: #fff;
}


.takecare-container{
 /* margin-top:30px; */
 -webkit-box-shadow: 0px 1px 14px -1px rgba(0,0,0,1);
-moz-box-shadow: 0px 1px 14px -1px rgba(0,0,0,1);
box-shadow: 0px 1px 14px -1px rgba(0,0,0,1);
width: 80%;
margin:auto
}

.takecare-title{
 padding-top: 15px;
 padding-left: 10px;
 padding-right: 10px;
 color: rgb(40, 94, 59)
}

.takecare-title h5 {
    border-bottom: solid 1px rgb(40, 94, 59);
    padding-bottom: 10px;
}

.takecare-text{
    padding:15px;
}

.takecare-img{
    width: 100%;
}
.takecare-img img {
    width: 100%
}

.takecare-footer{
    text-align: center;
}

.takecare-footer span {
    padding: 10px;
    background-color: rgb(40, 94, 59);
    color: white;
    cursor: pointer;
}
.takecare-footer a{
    text-decoration: none;
    color:white;
}

.takecare-footer ul{
    /* position: absolute; */
    left: 10px;
    bottom: 10px;
    list-style: none;
    display: flex;
    margin-top: 30px;
    margin-left: 15px;
    padding-bottom: 15px;
}

.takecare-footer ul li {
    width: 25px;
    margin: 5px;
    /* padding: 15px */

}
.takecare-footer ul li img{
    width: 100%;
    cursor: pointer;
}


.customService-dropdown {

}

.customService-dropdown-content{
    display: none;
    z-index: 100;
    position: absolute;
    background-color: white;
    color: black;
    margin-left: -16px;
    /* padding-left: 22px; */
    /* padding-right: 15px; */
    /* width: 100%; */
}

.customService-dropdown:hover .customService-dropdown-content{
    display: block !important;
}

.customService-dropdown-content a{
    display: block;
    /* color: black !important; */
    text-decoration: none;
    padding: 10px;
    padding-left: 32px;
}

.customService-dropdown-content a:hover{
    background-color: rgb(40, 94, 59);
    color: white !important
}